    margin: auto;
    background-color: rgba(0,0,0, 0.5);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 10000;

}

