.about-header {
    width: 100%;
    height: 500px;
    background: radial-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) ), url(/static/media/about.9d494c72.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    transition: 1s;
}

.header-title {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    height: 350px;
    padding-top: 100px;
    transition: 1s ease-in-out;
    margin-left: 1em;
}

.about-header p {
    font-size: 40px;
}

.header-pop {
    position: absolute;
    color: #fff;
    text-align: center;
    transition: 1s ease-in-out;
    opacity: 0;
    left: 50%;
    
}

.about-header:hover > .header-title {
    -webkit-transform: translateX(-6%);
            transform: translateX(-6%);
}

.about-header:hover > .header-pop {
    -webkit-transform: translateY(-210%);
            transform: translateY(-210%);
    opacity: 1;
}

.hover-space:hover ~ .header-pop {
    -webkit-transform: translateY(-210%);
            transform: translateY(-210%);
    opacity: 1;
}

.hover-space {
    width: 100%;    
    border: 170px solid transparent;
}

